import {BaseData} from "../../base/BaseData.js";

export let Element = new BaseData(
    
    "HTML标签",
    
    "HTML中的一些个人留意的标签知识",

    `知识点：

	行内元素: 不会自动换行的元素 注意:br是行内元素

	块级元素: 会自动换行的元素

	一份标准的HTML文档有哪几个必须的HTML标签：

		1、<html></html>
		
		2、<head></head>
		
		3、<title></title>
		
		4、<body></body>

	在HTML5中自关闭的斜线(/)无效的标签有(即不再需要自闭合(/)这个小尾巴的)：

		<area>

		<base> 

		<br> 

		<col> 

		<command>

		<embed>

		<hr>

		<img> 

		<input> 

		<keygen> 

		<link> 

		<meta>

		<param>

		<source> 

		<track> 

		<wbr>

		注意：如果要写上也是可以的。

具体的标签笔记：

	<meta></meta>
		<meta>元素表示那些不能由其它HTML元相关元素（<base>,<link>,<script>,<style>或<title>）之一表示的任何元数据信息。

		例子

			<!-- Defining the charset in HTML4 -->
			<meta http-equiv="Content-Type" content="text/html; charset=utf-8">

			<!-- In HTML5 -->
			<meta charset="utf-8">

			<!-- Redirect page after 3 seconds -->
			<meta http-equiv="refresh" content="3;url=http://www.mozilla.org/">

		知识点
			
			全局属性name在<meta>元素中具有特殊的语义；另外，在同一个<meta>标签中，name，http-equiv或者charset三者中任何一个属性存在是，itemprop属性不能贝使用。

			非兼容ASCII编码就是那些不会将8为代码点的0x20到0x70映射为Unicode代码点0x0020到0x007E的编码

		属性
			charset
				此特性声明当前文档所使用的字符编码，但该声明可以被任何一个元素的lang特性的值覆盖。
				此特性的值必须是一个符合IANA所定义的字符编码首选MIME名称（preferred MIME name）之一。尽管标准不要求必须使用某些特定的字符编码，但它还是给出了一些建议：
					
					鼓励使用UTF-8；
					
					不应该使用不兼容ASCII的编码规范，以避免不必要的安全风险：浏览器不支持他们（这些不规范的编码）可能会导致浏览器渲染html出错。比如JIS
					_C6226-1983,JIS_X0212-1990,HZ-GB-2312,JOHAB,ISO-2022系列,EBCDIC系列等文字。

					开发者应尽量避免使用UTF-32字符集对网页进行编码，因为不是所有的HTML5编码所发能够将其与UTF-16编码区分开来。

					声明的字符编码必须与页面保存所使用的编码相匹配，以避免乱码和安全漏洞。

					<meta> 元素必须包含在 <head> 元素中并且在HTML代码的前1024个字节内，因为某些浏览器在选择编码之前只查看前面这些字节。
					
					<meta>元素只是algorithm to determine the character set 中的一部分。 HTTP的Content-Type头部以及任何Byte-Order Marks元素都优先于此元素。
					
					强烈建议使用该属性定义字符编码. 如果未定义，某些跨脚本技术可能危害网页，如 UTF-7 fallback cross-scripting technique. 保持设置该属性以避免类似风险。
				
					<meta> 元素 的charset属性和以下HTML5内容<meta http-equiv="Content-Type" content="text/html; charset=IANAcharset">等效，其中 IANAcharset 包含了charset 一样的值。后者的语法仍然是允许的，但是不再推荐。

			content
				此属性的值用于提供给http-equiv或name属性使用。（注意，content内部如果有多个值的话，使用逗号分割，有时也使用分号）

			http-equiv
				这个枚举属性定义了能改变服务器和用户引擎行为的编译。

					"content-security-policy"内容安全策略
						它允许页面作者定义当前页的 内容策略。 内容策略主要指定允许的服务器源和脚本端点，这有助于防止跨站点脚本攻击。

					refresh
						这个属性指定:
						如果content 只包含一个正整数,则是重新载入页面的时间间隔(秒);
						如果content 包含一个正整数并且跟着一个字符串,则是重定向到指定链接的时间间隔(秒)

			name
				该属性定义文档级元数据的名称。如果以下其中一个属性设置了itemprop, http-equiv or charset ，就不能在设置这个属性了。

					application-name，定义正运行在该网页上的网络应用名称；
						浏览器可能会通过使用该属性去区分应用。它与<title>元素不同，后者通常由应用程序名称组成，但也可能包含特定信息，如文档名称或状态;
						简单的网页不应该去定义application-name meta标签。

					author，就是这个文档的作者名称，可以用自由的格式去定义；

					description，其中包含页面内容的简短和精确的描述。 一些浏览器，如Firefox和Opera，将其用作书签页面的默认描述。

					generator, 包含生成页面的软件的标识符。

					keywords, 包含与逗号分隔的页面内容相关的单词。

					creator，文档的创作者名称。

					robots，决定是否可以被网络爬虫搜索到。
						<meta name="robots">的content值：
							Value	值描述	适用公司（搜索引擎）
							index	允许网络爬虫找到该页面	所有公司（引擎）
							noindex	不允许网络爬虫找到该页面	所有公司（引擎）
							follow	允许网络爬虫通过link链接找到该页面	所有公司（引擎）
							nofollow	不允许网络爬虫通过link链接找到该页面	所有公司（引擎）
							noodp	不适用于open directory project的描述，即不会出现在搜索引擎的搜索结果中	Google, Yahoo, Bing
							noarchive	不准搜索引擎去缓存页面的内容	Google, Yahoo
							nosnippet	不准搜索引擎在搜索结果中显示任何页面相关的描述内容	Google
							noimageindex	不住这个页面显示在其它页面的索引页面中	Google
							noydir	不适用于Yahoo Directory description的描述，即不会出现在搜索引擎的搜索结果中	Yahoo
							nocache	等价于noarchive	Bing

					viewport, 它提供有关视口初始大小的提示，仅供移动设备使用。

						值的内容为： <meta name="viewport">

						Value	可能值	描述
						width	一个正整数或者字符串 device-width	以pixels（像素）为单位， 定义viewport（视口）的宽度。
						height	一个正整数或者字符串 device-height	以pixels（像素）为单位， 定义viewport（视口）的高度。
						initial-scale	一个0.0 到10.0之间的正数	定义设备宽度（纵向模式下的设备宽度或横向模式下的设备高度）与视口大小之间的缩放比率。
						maximum-scale	一个0.0 到10.0之间的正数	定义缩放的最大值；它必须大于或等于minimum-scale的值，不然会导致不确定的行为发生。
						minimum-scale	一个0.0 到10.0之间的正数	定义缩放的最小值；它必须小于或等于maximum-scale的值，不然会导致不确定的行为发生。
						user-scalable	一个布尔值（yes 或者no）	如果设置为 no，用户将不能放大或缩小网页。默认值为 yes。

						虽然标准化程度不高，但由于事实上的支配地位，大多数移动浏览器都尊重这一声明。
						不同设备和浏览器之间的默认值可能不同。
	<label></label>

		例子

			<div class="preference">
			    <label for="cheese">Do you like cheese?</label>
			    <input type="checkbox" name="cheese" id="cheese">
			</div>

			<div class="preference">
			    <label for="peas">Do you like peas?</label>
			    <input type="checkbox" name="peas" id="peas">
			</div>

		知识点

			将一个 <label> 和一个 <input> 元素放在一起会有以下好处

				标签文本不仅与其相应的文本输入在视觉上相关联; 它也以编程方式与它相关联。 这意味着，当用户点击到表单输入时，屏幕阅读器可以读出标签，使在使用辅助技术的用户更容易理解应输入哪些数据.
				
				你可以单击关联的标签来聚焦或者激活 input，以及 input 本身。这种增加的命中区域为激活 input 提供了方便，包括那些使用触摸屏设备的。

			其他

				标签标记的表单控件称为标签元素的标签控件。一个 input 可以与多个标签相关联。
				
				标签本身并不与表单直接相关。它们只通过与它们相关联的控件间接地与表单相关联。
 
				当点击或者触碰（tap）一个与表单控件相关联的 <label> 时，关联的控件的 click 事件也会触发。

		属性

			for
				可标记的 form 相关元素的 id ，在同一文档中作为label元素。 第一个这样的元素在文档中有一个ID匹配属性值，作为这个label元素的labeled control。
				注意： label元素可同时有属性和包含控件元素（contained control element），只要此属性指向包含控件元素。

			form
				表示label元素关联的form元素（它的表单拥有者）。此属性值必须是同一文档中<form> 元素的ID。如果未指定此属性值，<label>元素必须是<form>元素的后代。应用此属性值，你可以将label元素放置在文档的任何位置，而不仅仅是作为它的拥有者<form>元素的后代。 

	<form></form>

		例子

			<form action="" method="get" class="form-example">
			  <div class="form-example">
			    <label for="name">Enter your name: </label>
			    <input type="text" name="name" id="name" required>
			  </div>
			  <div class="form-example">
			    <label for="email">Enter your email: </label>
			    <input type="email" name="email" id="email" required>
			  </div>
			  <div class="form-example">
			    <input type="submit" value="Subscribe!">
			  </div>
			</form>

			<!-- 一个简单的表单，会发送一个 GET 请求 -->
			<form action="" method="get">
			  <label for="GET-name">Name:</label>
			  <input id="GET-name" type="text" name="name">
			  <input type="submit" value="Save">
			</form>

			<!-- 一个简单的表单，发送 POST 请求 -->
			<form action="" method="post">
			  <label for="POST-name">Name:</label>
			  <input id="POST-name" type="text" name="name">
			  <input type="submit" value="Save">
			</form>

			<!-- 使用 fieldset、legend，和 label 的表单 -->
			<form action="" method="post">
			  <fieldset>
			    <legend>Title</legend>
			    <input type="radio" name="radio" id="radio">
			    <label for="radio">Click me</label>
			  </fieldset>
			</form>

		属性

			action
				一个处理此表单信息的程序所在的URL。此值可以被 <button> 或者 <input> 元素中的 formaction 属性覆盖。

			enctype
				当 method 属性值为 post 时，enctype 就是将表单的内容提交给服务器的 MIME 类型 。可能的取值有：
				application/x-www-form-urlencoded：未指定属性时的默认值。
				multipart/form-data：此值用于一个 type 属性设置为 "file" 的 <input> 元素。
				text/plain：(HTML5)
				此值可以被 <button> 或者 <input> 元素中的 formenctype 属性覆盖。

			method
				浏览器使用这种 HTTP 方式来提交 表单. 可能的值有：
				post：指的是 HTTP POST 方法；表单数据会包含在表单体内然后发送给服务器.
				get：指的是 HTTP GET 方法；表单数据会附加在 action 属性的URI中，并以 '?' 作为分隔符，然后这样得到的 URI 再发送给服务器。如果这样做（数据暴露在 URI 中）没什么副作用，或者表单仅包含ASCII字符时，再考虑使用这种方法吧。
				dialog：Use when the form is inside a <dialog> element to close the dialog when submitted.
				此值可以被 <button>、<input type="submit"> 或 <input type="image"> 元素中的 formmethod 属性覆盖。

			name
				表单的名称。在 HTML 4 中，此用法不被推荐（作为替代，应该使用id）。HTML5中，一个文档中的多个表单当中，name必须唯一而不仅仅是一个空字符串。

			target
				一个名字或者说关键字，用来指示在提交表单之后，在哪里显示收到的回复. 在 HTML 4 里, 这是一个用于 frame 的名字/关键字. 在 HTML5 里，这是一个用于 browsing context 浏览器上下文 的名字/关键字（举例来说，标签页，窗口，或者行内 frame）。如下的关键字含有特别的含义：
				_self：在当前 HTML 4 或 HTML5 文档页面重新加载返回值。是默认值。译注：也就是说，如果此文档在一个frame中的话，self是在当前frame（document）中重新加载的，而不是整个页面（window）。
				_blank：以新的 HTML 4 或 HTML5 文档窗口加载返回值。
				_parent：在父级的 frame 中以 HTML 4 或 HTML 5 文档形式加载返回值，如果没有父级的frame，行为和_self一致。
				_top：如果是 HTML 4 文档：清空当前文档，加载返回内容；HTML5：在当前文档的最高级内加载返回值，如果没有父级，和_self的行为一致。
				iframename：返回值在指定 <iframe> 中显示。
				HTML5：此值可以被 <button>、 <input type="submit"> 或 <input type="image"> 元素中的 formtarget 属性覆盖。

	<iframe></iframe>

		例子

			<iframe id="inlineFrameExample"
			    title="Inline Frame Example"
			    width="300"
			    height="200"
			    src="https://www.openstreetmap.org/export/embed.html?bbox=-0.004017949104309083%2C51.47612752641776%2C0.00030577182769775396%2C51.478569861898606&layer=mapnik">
			</iframe>

		知识点

			每个嵌入的浏览上下文（embedded browsing context）都有自己的会话历史记录(session history)和DOM树。包含嵌入内容的浏览上下文称为父级浏览上下文。顶级浏览上下文（没有父级）通常是由 Window 对象表示的浏览器窗口。

		属性

			height
				以CSS像素格式HTML5，或像素格式HTML 4.01，或百分比格式指定frame的高度。默认值为150。

			width
				以CSS像素格式HTML5，或以像素格式HTML 4.01，或以百分比格式指定的 frame 的宽度。默认值是300。

			src
				被嵌套的页面的 URL 地址。使用 about:blank 值可以嵌入一个遵从同源策略的空白页。在 Firefox （version 65及更高版本）、基于 Chromium 的浏览器、Safari/iOS 中使用代码移除 iframe 的 src 属性（例如通过 Element.removeAttribute() ）会导致 about:blank 被载入 frame。

	<video></video>

		例子

			<video controls width="250">

		    		<source src="/media/examples/flower.webm"
		            type="video/webm">

		    		<source src="/media/examples/flower.mp4"
		            type="video/mp4">

		    		Sorry, your browser doesn't support embedded videos.

			</video>

		知识点

			HTML <video> 元素 用于在HTML或者XHTML文档中嵌入媒体播放器，用于支持文档内的视频播放。你也可以将 <video>  标签用于音频内容，但是 <audio> 元素可能在用户体验上更合适。

			和 <img> 元素的使用类似，在 src 属性里加入一个我们需要展示的视频地址，同时也可以用其他属性来定义视频的宽度高度、是否自动或者循环播放、是否展示浏览器默认的视频控件等信息。

			在 <video></video> 标签中间的内容，是针对浏览器不支持此元素时候的降级处理。

			浏览器并不是都支持相同的视频格式，所以你可以在 <source> 元素里提供多个视频源，然后浏览器将会使用它所支持的第一个源。

			<video controls>
	  			<source src="myVideo.mp4" type="video/mp4">
	  			<source src="myVideo.webm" type="video/webm">
	  			<p>Your browser doesn't support HTML5 video. Here is a <a href="myVideo.mp4">link to the video</a> instead.</p>
			</video>

			如果你没有指定 controls 属性，那么视频不会展示浏览器自带的控件，你也可以用 JavaScript 和 HTMLMediaElement API 来创建你自己的控件。
			
			HTMLMediaElement 会激活许多不同的事件，以便于让你可以控制视频（和音频）内容。
			
			你可以用CSS 属性 object-position 来调整视频在元素内部的位置，它可以控制视频尺寸适应于元素外框的方式。
			
			如果想在视频里展示字幕或者标题，你可以在 <track> 元素和 WebVTT 格式的基础上使用 JavaScript 来实现。

			时间偏移量目前是指定为float类型的值，表示偏移的秒数。

		属性

			autoplay
				布尔属性；指定后，视频会马上自动开始播放，不会停下来等着数据载入结束。

			buffered
				这个属性可以读取到哪段时间范围内的媒体被缓存了。该属性包含了一个 TimeRanges 对象。

			controls
				加上这个属性，Gecko 会提供用户控制，允许用户控制视频的播放，包括音量，跨帧，暂停/恢复播放。

			crossorigin
				该枚举属性指明抓取相关图片是否必须用到CORS（跨域资源共享）。 支持CORS的资源 可在 <canvas> 元素中被重用，而不会被污染。允许的值如下：
				
				anonymous
					跨域请求（即，使用 Origin: 的HTTP头）会被执行。但是不发送凭证（即，不发送cookie， X.509 证书或者 HTTP Basic 授权）。如果服务器不提供证书给源站点 (不设置 Access-Control-Allow-Origin: HTTP头)，图片会被 污染 并且它的使用会受限。
				
				use-credentials
					跨域请求A cross-origin request (i.e. with Origin: HTTP header) 会被执行，且凭证会被发送 (即， 发送一个 cookie, 一个证书和HTTP Basic授权会被执行)。如果服务器不提供证书给源站点 (通过Access-Control-Allow-Credentials: HTTP 头)，图像会被 污染 且它的使用会受限。
					不加这个属性时，抓取资源不会走CORS请求(即，不会发送 Origin: HTTP 头)，保证其在 <canvas> 元素中使用时不会被污染。如果指定非法值，会被当作指定了枚举关键字

			currentTime
				读取CurentTime返回一个双精度浮点值，指示以秒为单位的媒体的当前播放位置。如果video尚未开始播放，则会在开始播放后返回偏移量。通过CurentTime将当前播放位置设置为给定时间，会在加载媒体时将媒体查找到该位置（从指定的位置开始播放）。

				媒体正在播放的情况下，如果媒体缓冲区的数据已经过期（视频已经播放完），则 user agent有可能无法正常拿到数据。有些媒体可能有一个不以0秒开始的媒体时间线（不是从头开始播放的），因此应该将currentTime的时间设置在其数据失效之前。getStartDate() 这个方法可以用来确定媒体时间线起始的坐标。

			duration 只读
				一个双精度浮点值，它指示媒体的持续时间(总长度)，以秒为单位，在媒体的时间线上。如果元素上没有媒体，或者媒体无效，则返回的值为NaN。如果媒体没有已知终点(例如时间未知的实时流、网络广播、来自WebRTC的媒体等等)，那么这个值就是Infinity。

			height
				视频展示区域的高度，单位是CSS像素。

			width
				视频显示区域的宽度，单位是CSS像素。

			loop
				布尔属性；指定后，会在视频结尾的地方，自动返回视频开始的地方。

			muted
				布尔属性，指明了视频里的音频的默认设置。设置后，音频会初始化为静音。默认值是false,意味着视频播放的时候音频也会播放 。

			playsinline
				一个布尔属性，标志视频将被“inline”播放，即在元素的播放区域内。请注意，没有此属性并不意味着视频始终是全屏播放的。

			played
				一个 TimeRanges 对象，指明了视频已经播放的所有范围。

			preload
				该枚举属性旨在告诉浏览器作者认为达到最佳的用户体验的方式是什么。可能是下列值之一：
					none: 提示作者认为用户不需要查看该视频，服务器也想要最小化访问流量；换句话说就是提示浏览器该视频不需要缓存。
					metadata: 提示尽管作者认为用户不需要查看该视频，不过抓取元数据（比如：长度）还是很合理的。
					auto: 用户需要这个视频优先加载；换句话说就是提示：如果需要的话，可以下载整个视频，即使用户并不一定会用它。
					空字符串：也就代指 auto 值。
				假如不设置，默认值就是浏览器定义的了 （即，不同浏览器会选择自己的默认值），即使规范建议设置为 metadata。

			poster
				一个海报帧的URL，用于在用户播放或者跳帧之前展示。如果属性未指定，那么在第一帧可用之前什么都不会展示；之后第一帧就像海报帧一样展示。

			src
				要嵌到页面的视频的URL。可选；你也可以使用video块内的 <source> 元素来指定需要嵌到页面的视频。

	<audio></audio>

		例子

			<!-- Simple audio playback -->
			<audio src="http://developer.mozilla.org/@api/deki/files/2926/=AudioTest_(1).ogg" autoplay>
  				Your browser does not support the <code>audio</code> element.
			</audio>

			<!-- Audio playback with captions -->
			<audio src="foo.ogg">
  				<track kind="captions" src="foo.en.vtt" srclang="en" label="English">
  				<track kind="captions" src="foo.sv.vtt" srclang="sv" label="Svenska">
			</audio> 

			<audio controls="controls">
  				Your browser does not support the <code>audio</code> element.
  				<source src="foo.wav" type="audio/wav">
			</audio>

		知识

			HTML <audio> 元素用于在文档中表示音频内容。 <audio> 元素可以包含多个音频资源， 这些音频资源可以使用 src 属性或者<source> 元素来进行描述； 浏览器将会选择最合适的一个来使用。对于不支持<audio>元素的浏览器，<audio>元素也可以作为浏览器不识别的内容加入到文档中。

			时间偏移量目前是指定为float类型的值，表示偏移的秒数。

		属性

			autoplay
				布尔属性；如果指定（默认值为"false"！）；指定后，音频会马上自动开始播放，不会停下来等着数据载入结束。

			buffered
				你可以通过该属性获取已缓冲的资源的时间段信息。该属性包含一个 TimeRanges 对象。

			controls
				如果设置了该属性，浏览器将提供一个包含声音，播放进度，播放暂停的控制面板，让用户可以控制音频的播放。

			loop
				布尔属性；如果指定，将循环播放音频。

			muted
				表示是否静音的布尔值。默认值为false，表示有声音。

			played
				一个TimeRanges 对象，表示所有已播放的音频片段。

			preload
				枚举属性，让开发者自行思考来示意浏览器使用何种加载方式以达到最好的用户体验。可以是以下属性之一：
					none: 示意用户可能不会播放该音频，或者服务器希望节省带宽；换句话说，该音频不会被缓存；
					metadata: 示意即使用户可能不会播放该音频，但获取元数据 (例如音频长度) 还是有必要的。
					auto: 示意用户可能会播放音频；换句话说，如果有必要，整个音频都将被加载，即使用户不期望使用。
					空字符串 : 等效于auto属性。
				假如不设置，默认值就是浏览器定义的了（不同浏览器会选择自己的默认值）， 即使规范建议设置为 metadata.

				使用备注：
					autoplay 属性优先于 preload 假如用户想自动播放视频，那么很明显浏览器需要下载视频。同时设置autoplay 和 preload属性在规范里是允许的。
					规范没有强制浏览器去遵循该属性的值；这仅仅只是个提示。

			src
				嵌入的音频的URL。 该URL应遵从 HTTP access controls. 这是一个可选属性；你可以在audio元素中使用 <source> 元素来替代该属性指定嵌入的音频。
				
			volume
				音频播放的音量。值从0.0 (无声) 到 1.0 (最大声).`,
);