import {BaseData} from "../../base/BaseData.js";

export let Promise = new BaseData(
    'JavaScript中的Promise',
    '如题，讲解用法',
    `1.回调地狱

	有时候为了实现某些逻辑，会写出层层嵌套的回调函数，例如：

	var halloworld = function(delaytime, callback){
		setTimeout(()=>{
			console.log("hallo world");
			callback();
		}), delaytime);
	};

	halloworld(1, ()=>{
		hallworld(2, ()=>{
			halloworld(3, ()=?{
				...
			});
		});
	});

	上面就实现了一个回调地狱。


2.JavaScript中的Promise

	为了解决回调地狱问题，javascript 中诞生了一个promise函数。

	当然除了解决回调地狱的问题，还有如下好处：

	1）确保了回调函数不会被调用直到当前的事件循环结束。

	2）确保了无论Promise中的函数的完成情况，只要添加到了then中的成功或失败函数都一定会按顺序执行调用。但是链式调用中只要有一个then回调中发生了错误，就会持续向后寻找第一个catch回调或者拥有错误回调的then函数然后执行该回调，过程中的then的链式调用都会跳过，然后从该回调函数开始，若后续还有then函数，就会恢复正常的链式调用流程并且前述情况也继续适用。

3.Promise语法：

	new Promise(executor);

	executor：
	A function that is passed with the arguments resolve and reject. The executor function is executed immediately by the Promise implementation, passing resolve and reject functions (the executor is called before the Promise constructor even returns the created object). The resolve and reject functions, when called, resolve or reject the promise, respectively. The executor normally initiates some asynchronous work, and then, once that completes, either calls the resolve function to resolve the promise or else rejects it if an error occurred. If an error is thrown in the executor function, the promise is rejected. The return value of the executor is ignored.

	example：

	const myFirstPromise = new Promise((resolve, reject) => {
	  // do something asynchronous which eventually calls either:
	  //
	  //   resolve(someValue)        // fulfilled
	  // or
	  //   reject("failure reason")  // rejected
	});

4.关于 then 函数：

	语法：
	p.then(onFulfilled[, onRejected]);

	p.then(value => {
	  // fulfillment
	}, reason => {
	  // rejection
	});

	关于then的个人理解说明:

	1.then函数是对象方法，返回一个新的Promise对象。
	2.当前一个Promise对象的resolve或者reject函数被调用后，才会执行该then函数中的onFulfilled或者onRejected方法。
	3.前一个Promise对象调用resovle(result)或reject(result)后，该result值会被记录下来，直到then方法被调用后作为第一个参数传入到onFulfilled或onRejected方法上。
	4.Promise严格控制了then函数的每一次调用都是上一次事件循环结束之后才会进行调用。
	5.在onFulfilled函数中return值有两种情况：

		1）new Promise((resolve,reject)=>{
			resolve('a');
		}).then(result=>{
			return result;	// equal to 'return Promise.resolve(result)';
		}).then(result=>{
			console.log(result);
		});

		2) new Promise((resolve,reject)=>{
			resolve('a');
		}).then((result)=>{
				return new Promise((resolve,reject)=>{
					setTimeout(function(){
						resolve(result);
					},3000);
				});
		}).then((result)=>{
			console.log(result);
		});

	其中第一种情况是直接在then中返回了一个值，这种情况下等价于调用了Promise.resolve(value);直接返回了一个fulfilled的Promise对象。
	第二种情况则是自己重写了一个新的Promise，这样子可以达到延迟处理的目的。

5.关于 then 的链式调用：

	new Promise((resolve, reject) => {
	    console.log('Initial');

	    resolve();
	})
	.then(() => {
	    throw new Error('Something failed');
	        
	    console.log('Do this');
	})
	.catch(() => {
	    console.error('Do that');
	})
	.then(() => {
	    console.log('Do this, no matter what happened before');
	});

	其中 catch(callback) 可以替换成 then(null, failureCallback), 前者是后者的等效写法。

	*以下例子可以更方便的理解整个链式调用会发生的流程
	new Promise((resolve, reject) => {
	    console.log('Initial');

	    resolve();
	})
	.then(() => {
	    
		// 下面可以用throw new Error的方式也可以用 new Promise的方式来返回一个错误。

	    //throw new Error('Something failed');
	        
	    return new Promise((resolve,reject) => {
	    	reject();
	    });

	    console.log('Do this');
	})
	.then(()=>{
		
		console.log('Do this before catch');
	})
	.then(null ,() => {
	    console.error('Do that');
	})
	.then(() => {
	    console.log('Do this, no matter what happened before');
	});`
);