import {BaseData} from "../../base/BaseData.js";

export let Grammer = new BaseData(
    'Vue的一些概念',
    '如题，主要记录一些Vue相关的概念',
    `Vue的使用：

	1.简单粗暴：直接在官网下载并在HTML代码中用<script>导入某个确定版本的vue.js文件

	2.使用Webpack：npm install vue -S 安装vue模块

创建Vue对象：
	
	var vm = new Vue({
		tag : value
	});

	只要上述对象被实例化之后，Vue就起到了作用，这里Vue在MVVM中扮演的角色就是VM的作用

	其中tag：

		*** el : 这个new的Vue实例所对应的HTML id的页面元素是哪个。
			* 注意：在Vue1.0可以在body上添加ID，在2.0开始后就不可以用同样的方式来标记了
  
		*** data : data属性中，存放的是el区块中要用的数据，也就是另一个Json对象，如	
			data : {
				msg : "欢迎测试使用Vue"
			}

		*** methods : 存放的是el区块中可以调用的函数

		*** filters : 存放的是el区块中定义的“私有过滤器”

		*** directives : 存放的是el区块中定义的“私有指令”

		*** watch: 存放的是用于监听data里数据变化的相关函数，也是一个对象，如
			...
			data:{
				testWatch: 10,
			},
			watch:{
				testWatch(newValue, oldValue){
					// newValue 和 oldValue 分别就是该属性的新和旧两个值
					console.log("Has detected testWatch value changed");
				}
			}
			...

		*** computed: 定义一些称为 “ 计算属性 ” 的属性，该属性本质上就是一个函数，但是使用的时候并不会去调用这个函数而是直接当成一个属性来使用，有点类似于C#的getter访问器。例：
			 ...
			 data:{
			 	a: 2,
			 	b: 3,
			 },
			 computed:{
			 	c:function(){		// c本质上也是一个data，可以和data一样使用，只是只能取值不能赋值
			 		return this.a + this.b;
			 	}
			 }
			* 注意: 这个computed属性是会被缓存起来的，如果函数内部的数据没有发生过变化，这个属性的值就是缓存的

		然后在对应的HTML元素的区域中，直接使用{{msg}},则最终该标签会被直接替换为data中的值，如
		* 注意，{{value}} 又叫插值表达式

		<div id="testVue">
			欢迎测试使用Vue
		</div>

		等价于

		<div id="testVue">
			{{msg}}
		</div>

		*** 

Vue相关的标签的属性(也叫Vue指令)：

	注意，所有Vue指令在规则上都默认要以“V-”开头，包括自定义指令也一样。

	v-cloak :
	 	放在标签里，就可以解决该标签下插值表达式闪烁的问题。

	v-text :
		用法和插值表达式类似，但是这个属性直接赋值来使用，如
		<div v-clock> {{data}} <div> 效果和
		<div v-text="data"><div> 效果一样，但:
		
		（1）v-text默认没有闪烁问题，用插值表达式一定要用v-clock来解决闪烁的问题 

		（2）其次v-text会覆盖元素中原本的所有内容，而使用插值表达式只会替换掉插值表达式指定的值。
		
		（3）注意:
			无论使用插值表达式还是使用v-text属性都是只会把内容当作字符串来替换，而不会解析html的标签的。

	v-html:
	message可以包含html标签，来展示一个页面的内容。特别是，如果后端返回包含了标签的内容，可以转化为html页面的形式展示。例：
		<div v-html="message">{{message}}</div>
		var vm = new Vue({
			el: "#div1",
		  	data:{
		    	message:"<input type="button" value='text html'>"
		  	}
		});

	v-bind:
		（1）用于绑定属性的指令，用法如
			var vm = new Vue({
				el:	"#testVBind",
				data: {
					myTitle:"测试input标属性值是否绑定"
				}
			})
		<input type="button" value="测试v-bind" v-bind:title="myTitle" />
		
		（2）注意:
			在v-bind绑定的属性值的内容会变成一个表达式来执行返回一个结果，也就是不是单纯的按照标签替换，如
			<input type="button" value="测试v-bind" v-bind:title=" myTitle + 'test' " />是合法的，最终title="测试input标属性值是否绑定test"

		（3）关于v-bind还有一种简写形式，如：
			<input type="button" value="测试v-bind" :title="myTitle" /> ，也就是不需要把v-bind写出来，直接一个冒号就可以了。

		（4）v-bind只能实现数据的单向绑定（即修改了vm实例中属性的值会直接立刻映射到html元素中去）

	v-on:
		用于绑定事件，用法如
		var vm = new Vue({
			el:"#testButton",
			methods:{
				show : function(){
					alert("v-on 事件测试成功")
				}
			}
		});

		 <input id="testButton" type="button" value="按钮" :title="测试vue事件" v-on:click="show">

		 一定要提前声明好methods方法里的东西。

		（1）关于v-on也有一个缩写形式，如：
			<input id="testButton" type="button" value="按钮" :title="测试vue事件"  @click="show">，也就是不需要把v-on写出来，直接一个@就可以了。
			* 注意，即使加了@，也是可以直接在click内部直接写执行语句的，如<input id="testButton" type="button" value="按钮" :title="测试vue事件"  @click="alerat("test!")">，其中加了小括号就是可以给函数传递参数了，包括自己定义的函数也可以，比如上面的v-on:click="show(param)"

		（2）事件修饰符：
			用法：例如 @click.stop="show" 这样就实现了阻止冒泡的功能
			
			stop：阻止冒泡
			
			prevent：阻止默认行为，如<a href="www.google.com" @click.prevent="doSomething"/> ，这样点击了超链接就不会立刻自动跳转到谷歌的官网了。

			capture：启用事件捕获

			self：只启动由自身发起的事件

			once：只触发一次的事件

			self和stop的区别：self只阻止了自己身上的事件接收，但是并没有阻止整个冒泡行为，而stop会阻止整个冒泡的行为。

		（3）键盘事件key的相关事件修饰符：
			如 @keyup.enter="dosomething" 当敲击回车键的时候调用该事件。
			* 注意，本质上enter只是给键盘码起了别名，等效于 @keyup.13 = "dosomething"，还有一些框架默认提供的常用别名可在官网查。
			
			给没有提供的按键对应的按键码起别名,例：Vue.config.keyCodes.f2 = 113;
			则f2就成为了别名，可以和enter一样用了

	v-model:
		用于双向的数据绑定，使用方式例如：
		<input type="text" v-model="someDataTag">
		效果：这样之后每次input的text中的内容发生变化时也会对应的将内容同步到Vue实例中的data中的对应someDataTag名字的属性上去，导致其它饮用了该属性的html元素显示的内容同步发生变化。

		* 注意，v-model指令只能运用在表单元素中，表单元素有如： input(radio, text, address, email...) select checkbox textArea ...

	v-for:
		1.用于遍历普通数组，如：

			<p v-for="item in list">{{item}}</p>

			var vm = new Vue({
				el:"#div1",
				data:{
					list:[1,2,3,4]
				}
			});

			* 注意，v-for右边的表达式也可以认为是调用了array.foreach()的方式进行的调用，如：

			<p v-for="(item, index) in list">item:{{item}}'s index is {{index}}</p>

			var vm = new Vue({
				el:"#div1",
				data:{
					list:[1,2,3,4]
				}
			});

		2.用于遍历对象数组，用法和遍历普通数组基本一致除了value都是对象,如：
			<p v-for="(item, index) in list">item name:{{item.name}}'s index is {{index}}</p>

			var vm = new Vue({
				el:"#div1",
				data:{
					items:[{name:"a"},{name:"b"}]
				}
			});

		3.用于遍历单个对象，如：

			<p v-for="(value, key) in man">value is:{{value}}'s' key is {{key}}</p>

			var vm = new Vue({
				el:"#div1",
				data:{
					man:
					{
						name: james,

						sex: male,

						age: 12,
					}
				}
			});

			* 注意，如果是遍历对象的话，在第三个位置还有一个index：
			<p v-for="(value, key, index) in man">value is:{{value}}'s' key is {{key}}, index is {{index}}</p>

		4.迭代数字，如：
			<p v-for="var currentTimes in 10">currentTimes is:{{currentTimes}}</p>

		5.关于绑定自动生成的对象的唯一性，使用v-bind:key = "string/number", 如：
			// 借用2中的数组对象
			<p v-for="(item, index) in list" :key="item.name">item name:{{item.name}}'s index is {{index}}</p>
			* 在组件使用时/在插入和排序等时候有用。

	v-if和v-show:
		1.用于控制html元素的现实与隐藏，如：
		<div id="div1">
			<div v-if="divSwitch"></div>
			<div v-show="divSwicth"></div>
		</div>
		var vm = new Vue({
			el:"#div1",
			data:{
				divSwitch: true,	//true的时候就显示
			}
		});

		v-if和v-show的区别：v-if在隐藏元素的时候会直接把元素移除，而v-show的隐藏则是在属性上把display改为none。
		v-if：有较高的资源切换的消耗，适用于不需要频繁切换的UI元素
		v-show：有较高的初始消耗，适用于频繁切换的UI元素

	v-focus:
		用于自动获取焦点，如：
		<input type="text" v-focus>

Vue的静态函数：
	
	1.过滤器

	Vue.filter("name", function(data, ...){
		// name就是定义给插值表达式调用的名字，data 就是在插值表达式中管道符前半的值
		// ...可变长参数就可以作为调用时 {{ data | function(...) }} 中function传来的额外参数
	});

	在html元素中调用方法（使用插值表达式）：{{ data | function1 | function2 | ... }} 例：

	...
	<div id="div1">  {{ testFIliterData | testFiliter }} </div>
	...
	
	Vue.filter("testFiliter1", function(data){
		return data.replace(/a/g,"");
	});

	Vue.filter("testFiliter2", function(data){
		return 
	});

	var wm = new Vue({
		el:"#div1",
		data:{
			testFiliterData: "aaaabbbbssssjjjj";
		} 
	});

	*注意，如果私有过滤器和全局过滤器重名了，则会优先调用私有过滤器的方法

	2.自定义指令（也叫钩子函数）

	Vue.directive(directiveName, object);

	directiveName: 指令的名字，注意在定义的时候不需要加“v-”前缀，但是在使用的时候需要
	object: 是一个对象，这个对象上面有一些指令相关的函数，这些函数可以在特定的阶段执行相关的操作（重要），如：
	Vue.directive("focus", {

		// * 注意，所有这里定义的函数的第一个参数el就是该元素本身，也就是一个原生的DOM元素对象，拥有所有DOM元素的方法

		// * 注意2。 所有这里定义的函数的第二个参数binding是框架自己传入的一个对象，里面有函数可以使用的各种数据
		// binding的属性有:
		//	name: 指令名，不包括 v- 前缀
		//	value: 指令的绑定值，例如 v-focus=“1+1”，则这里的绑定值就是2（因为算出了1+1的结果）
		//	expression: 绑定值的字符串，例如 v-focus="1+1"，则这里的绑定值结果就是字符串的“1+1”
		//	注意，如v-focus=“‘red’” 和 v-focus=”red“ 这两个指令，后面这个会去寻找是否有定义red的这个变量然后去读取这个变量的值，而前面的会直接把字符串当成值传入。当使用前面的这个语句的时候，value中读取出来的值没有添加双引号，而expression中读取出来的值还会额外添加双引号。


		bind: function(el, binding){
			// 每当指令绑定到元素上的时候，会立即执行这个函数，只执行一次（一般在这个时点用来更改DOM元素的样式等纯配置相关数据）
			
			el.focus();	//调用了原生的DOM元素对象的focus方法
		},
		inserted: function(el, binding){
			// 每当元素插入到DOM中的时候，会立即执行这个函数，只执行一次（也就是元素实际已经放到了页面中的时候，这时候操作原生的DOM元素对象的方法才会有效果）
			...
		},
		update: function(el, binding){
			// 每当VNode更新的时候会执行这个函数，可能会执行多次
			// 其中VNode是Vue框架自己提供的一个概念，待查实现原理
			...
		}
	});

	*注意，如果私有指令和全局指令重名了，则会优先使用私有指令

	*如果函数对应的作用Tag仅为bind和updated，那么就可以直接省略对象而直接传入函数即可，如：
		Vue.directive("focus", function(el, bindings){
			// 相当于只绑定了bind和update
		});

Vue的语法：

	1.关于在Vue实例中定义了函数如何取Vue实例中的data中的值的问题，例:
		var vm = new Vue({
			data:{
				a:1,
				b:"2",
			}
			methods:{
				// ES6 定义函数法
				test(){
					alert(this.a);
				},
			}
		});
	这里的this指代的就是这个Vue对象自身。

	*** 注意，一个很关键的重点：Vue的实例vm对象上的data的数据发生变化时，这个变化会立刻同步到页面之中。

	2.在Vue中使用样式：
		1. 使用v-bind直接给class传递一个包含样式类名字符串的数组，如：
			
			...
			.testStyle{
				background: "red",
				border: 20px
				...
			}
			...

			 <div id="div1" :class=['testStyle']></div>

		2. 在数组中使用三元表达式，如：
			...

			...
			.testStyle{
				background: "red",
				border: 20px
				...
			}
			...

			 <div id="div1" :class=[ ifUseTestStyle?'testStyle':'']></div>

			var vm = new Vue({
				el: "#div1",
				data: {
					ifUseTestStyle: true
				}
			});

			* 三元表达式的写法可以被一个对象所替代，比如上面同一个例子中可以改为：
			<div id="div1" :class=[ {"testStyle": ifUseTestStyle}]></div>
			其中键的值是class的类名，值是data中对应的数据。

		3.直接使用一个对象，如：
			...

			...
			.testStyle{
				background: "red",
				border: 20px
				...
			}

			.testStyle2{
				...
			}
			...

			 <div id="div1" :class={"testStyle": ifUseTestStyle, "testStyle2": ifUseTestStyle2}></div>

			var vm = new Vue({
				el: "#div1",
				data: {
					ifUseTestStyle: true,
					ifUseTestStyle2: true
				}
			});

		* 注意，无论是在数组中使用对象还是直接使用对象，键值对的值一定是按照boolean的标准在使用，意思是是否打开这个样式

		4.直接讲数组或者对象整个定义在data中来读取，如：
			...

			...
			.testStyle{
				background: "red",
				border: 20px
				...
			}

			.testStyle2{
				...
			}
			...

			 <div id="div1" :class=”styleSwitch“></div>

			var vm = new Vue({
				el: "#div1",
				data: {
					styleSwitch:{
						"testStyle": true, 
						"testStyle2": false
					}
				}
			});
		例子中介绍的应该还比较易懂，就不多赘述了。

		5.使用内联样式：
			<div id="div1" :style=“testStyle”></div>
			var vm = new Vue({
				el: "#div1",
				data: {
					“testStyle”:{
						color: red, 
						"font-weight": 200
					}
				}
			});

			使用内联样式的方法比较简单，类似于直接把内联样式的定义放到了Vue中来处理。

			可以使用数组的方式同时为内联样式添加两个以上的样式，如：
			<div id="div1" :style=“[testStyle, testStyle2]”></div>
			var vm = new Vue({
				el: "#div1",
				data: {
					“testStyle”:{
						color: red, 
						"font-weight": 200
					}
					“testStyle2”:{
						"font-style": italic
					}
				}
			});


Vue实现数据请求（使用第三方）：

	 vue-resource 和 “axios”的第三方包用于实现数据的请求。

	 * 注意，在导入第三方包的时候，vue-resource要在vue的库文件之后。 vue-resource依赖于vue。

	 用法：
	 	var vm = new Vue({
	 		el:"#div1",

	 		data:{},

	 		methods:{
	 			getInfo(){
	 				// 发起get请求, 注意successCallback一定要有，errorCallback可以没有
	 				// this.$http.get("/someURL", [option]).then(successCallback, errorCallback);

	 				this.$http.get("http://www.baidu.com/...").then(function(result){
	 					console.log(result);
	 				});
	 			},

	 			postInfo(){
	 				// 发起post请求, 注意手动发起的表单请求默认没有表单格式，所以有的服务器处理不了
	 				// 也就是没有 application/x-www-form-urlencoded 格式，需要添加一个option
	 				// this.$http.post("somwURL", [body], [option]).then(successCallback, errorCallback);

	 				// 通过option参数设置提交的类型为表单数据格式
	 				this.$http.post("http://www.baidu.com/...", {}, {emulatedJSON : true}).then(function(result){
	 					console.log(result);
	 				});
	 			},

	 			jsonpInfo(){
	 				// 发起jsonp请求
	 				// this.$http.jsonp("someURL", [option]).then(successCallback, errorCallback);

	 				this.$http.jsonp("http://www.baidu.com/...").then(result =>{
	 					console.log(result);
	 				});
	 			}
	 		}
	 	})

	可以使用全局配置根路劲的方法：
		Vue.http.options.root = "...";

	可以使用全局配置是否使用表格上传方式的方法：
		Vue.http.options.emulateJSON = true;

Vue中的动画：
	
	v-enter -> v-enter-active -> v-enter-to;

	v-leave -> v-leave-active -> v-leave-to;

	使用Vue动画步骤：
		
		1. 使用transition元素把需要被动画控制的元素包裹起来，transition元素是Vue官方提供的元素

		2. 自定义两组样式来控制transition来实现transition内部的过度动画

		3. 如果要对v-for创建的列表实现动画,则需要使用transitionGroup包裹起来,并且一定要指定 :key

		4. 注意,使用transitionGroup包裹的html标签会在外层默认添加一个<span></span>标签,如果要取消这个span标签,那么就要使用 tag="外层的html标签名" 这个属性来替换掉<span>标签.

		5. <transition appear> ... </transition> appear属性可以实现登场播放一次的效果

		6. <transition mode="out-in"> ... </transition> 可以用来对放在其中的component组件的切换做切换效果的控制。

		7. Vue的动画控制是分enter和leave两个阶段的，而enter和leave两个阶段又分别由v-if和v-show的false->true的瞬间和true->false的瞬间进行控制。

		8. 父组件可以在引用子组件的时候，可以通过v-bind的形式把数据和函数传递给子组件。例:

			...
			<mycomponent :parentmsg="msg"></mycomponent>
			<mycomponent @parentFunc="myfunc"></mycomponent>
			...

			<template id="">
				<div> {{parentmsg}} </div>
				<input type="button" value="测试子组件函数传值" @click=""/> 
			</template>

			var vm = new Vue({
				el:"#div",
				data:{
					msg:"test parent msg"
				},
				methods:{
					myFunc(...){
						console.log("this is parent func "+...);
					}
				},
				components:{
					mycomponent:{
						data(){
							return {
								someData:"xxx"
							};
						},
						methods:{
							childFunc(){
								this.$emit("parentFunc", ...)	// 使用 this.$emit(funcName, params)来调用父对象的函数，然后可以通过后面的可变长参数达到给父对象传值的目的
							}
						}
						template:"",
						props:["parentmsg"]	//注意props是一个数组
					}
				}
			});

			* 注意，data是属于组件的私有数据，是可读可写的，而props是属于父组件继承的数据，是只读的。

Vue中的组件:

	概念:
		1. 模块化: 代码逻辑进行划分
		2. 组件化: 界面功能进行划分
		3. 组件和Vue实例对象一样，也是可以定义自己的生命周期函数的，和Vue对象的生命周期完全相同

	创建全局组件:
		1. 生成一个全局组件
			// 记得返回的变量很重要
			var con = Vue.extend({
				template: "<h1>这是使用Vue.extend创建的组件</h1>"	// 通过template属性, 指定组件要展示的html结构
			});

		2. 注册一个全局组件
			// Vue.component(名字, Vue.extend生成的组件实例)
			Vue.component("myCom1", con); 

		3. 简便写法
			Vue.component("myCom1", {
				template: "<h1>这是使用Vue.extend创建的组件</h1>"
			});

		4. 更合适的写法:

			<template id="name">
				<h1>这是使用Vue.extend创建的组件</h1>
			</template>

			Vue.component("myCom1", {
				template: "#name"
			});

		5. 定义私有组件:

			var vm = new Vue({
				el:"#div1",
				data:{},
				methods:{},
				components:{
					myCom1:{
						template:"#name"
					}
				}
			})

		6. 定义组件自己的数据和函数:
			// 组件可以定义自己的data,不过data一定要定义成一个函数,然后函数中一定要返回一个对象,然后这个对象就是和Vue对象中的data对象使用方式一致了. 这么做的根本原因是想让data的数据本身达到独有化，因为组件是通用的。
			// 函数定义和用法和Vue对象并没有区别
			Vue.component("myCom1", {
				template: "<h1>这是使用Vue.extend创建的组件</h1>",
				data:function(){
					return {};
				}
				methods:{
					functionName:function(){

					}
				}
			});

		7. 用Vue提供的标签调用组件:
			<component :is="‘myCom1’"></component>
			其中is的值要是一个组件的名称的字符串，这里用的是上面例子中的myCom1。
			基于这个特性，可以在is中实现一个变量来动态改变该处现实的组件，如
			...
			var vm = new Vue({
				el:"#div",
				data:{
					comName: "myCom1"
				}
			});

			<component :is="comName"></component>
			...
			然后只要动态修改comName值所指向的字符串就可以动态展示组件了

		* 注意: template模板中的html内容只能有一个根元素

		8.使用插槽

		使用v-slot命令实现插槽（例子）:

		...
		<template id="test">
			<a>
				<slot></slot>
			</a>
			<slot name="bottom"></slot>
		</template>
		...

		Vue.component("myCom", {
			template:"#test"
		});
		...

		<my-com>
			<p> 这里的内容会放到插槽中。 </p>
			<template v-slot:bottom>
				这里的内容会被放到模板的最后。
			</template>
		</my-com>

		9.插槽向父组件传值

		...
		<template id="test">
			<a>
				<slot></slot>
			</a>
			<slot name="bottom" v-bind:myNameOutSide="myName">
				{{ myName }}
			</slot>
		</template>
		...

		Vue.component("myCom", {
			template:"#test",
			data(){
				return {
					myName: "我的名字"
				}
			}
		});
		...

		<my-com>
			<p> 这里的内容会放到插槽中。 </p>
			<template v-slot:bottom="bottomProps">
				我的名字是:{{bottomProps.myNameOutSide}}
			</template>
		</my-com>

		10.解构插槽:

		作用域插槽的内部工作原理是将你的插槽内容包括在一个传入单个参数的函数里：

		function (slotProps) {
		  // 插槽内容
		}
		这意味着 v-slot 的值实际上可以是任何能够作为函数定义中的参数的 JavaScript 表达式。所以在支持的环境下 (单文件组件或现代浏览器)，你也可以使用 ES2015 解构来传入具体的插槽 prop，如下：

		<current-user v-slot="{ user }">
		  {{ user.firstName }}
		</current-user>
		这样可以使模板更简洁，尤其是在该插槽提供了多个 prop 的时候。它同样开启了 prop 重命名等其它可能，例如将 user 重命名为 person：

		<current-user v-slot="{ user: person }">
		  {{ person.firstName }}
		</current-user>
		你甚至可以定义后备内容，用于插槽 prop 是 undefined 的情形：

		<current-user v-slot="{ user = { firstName: 'Guest' } }">
		  {{ user.firstName }}
		</current-user>

		11.插槽名的缩写
		2.6.0 新增

		跟 v-on 和 v-bind 一样，v-slot 也有缩写，即把参数之前的所有内容 (v-slot:) 替换为字符 #。例如 v-slot:header 可以被重写为 #header：

		<base-layout>
		  <template #header>
		    <h1>Here might be a page title</h1>
		  </template>

		  <p>A paragraph for the main content.</p>
		  <p>And another one.</p>

		  <template #footer>
		    <p>Here's some contact info</p>
		  </template>
		</base-layout>

		注意，不能对解构使用插槽重写，除非指定名称：
		<!-- 这样会触发一个警告 -->
		<current-user #="{ user }">
		  {{ user.firstName }}
		</current-user>
		如果你希望使用缩写的话，你必须始终以明确插槽名取而代之：

		<current-user #default="{ user }">
		  {{ user.firstName }}
		</current-user>

	使用全局组件:
		// 注意,注册时使用的是驼峰命名,在使用的时候就需要用‘-’来拆开
		<my-com1> </my-com1>

Vue中直接获取一个html标签对象:

	...
	<div id="testRef" ref="testRef"></div>
	...

	var vm = new Vue({
		el:"#testRef",
		data:{},
		methods:{
			TestRef(){
				console.log(this.$refs.testRef.innerText);	//使用 .$refs 就可以直接获取到标记了ref的html标签
			}
		}
	});

	也可以用ref来获取组件对象，这个方法就可以用来读取组件的data数据了，如:

	var login = {
		template: "<h1>测试组件</h1>",
		data(){
			return {
				msg: "some msg"
			}
		},
		methods:{
			show(){
				console.log("调用了组件的method")
			}
		}
	}

	<login ref="mylogin"></login>

	var vm = new Vue({
		el:"#div1",
		data:{},
		methods:{
			getElement(){
				console.log("mylogin data msg is " + this.$refs.mylogin.msg);
				this.$refs.mylogin.show();
			}
		}
	});

vue-router 前端路由的使用:

	1.安装vue-router和vue-resource一样，需要导入vue-router的第三方js文件。

	2. var routerObj = new VueRouter({
		routes:[		//路由匹配规则，注意这里没有r
			{path: "/", redirect: "/login" },
			{path: "/login", component: componentObject1 },
			{path: "/register", component: componentObject2 }
			// 每个路由规则都是一个对象，这个对象一定有两个属性。但还有别的属性如下:
			// 属性1 path : 表示监听哪个路由链接地址。
			// 属性2 component : 表示，如果路由是前面匹配的path，则展示component属性对应的那个组件,注意这里只能放一个组件的模板对象，而不是放一个名称的字符串。
			// 属性3 redirect: 也是一个path,当用户输入了path的路径,则会强制转移到redirect的路径
		],

		linkActiveClass: "className" // 这个用来给router-link标签设置选中情况下的样式的类名 			
	});

	3. 在Vue对象中注册刚才的router对象:
		var vm = new Vue({
			el: "#div1",
			data: {},
			methods: {},
			router: routerObj	// 注册到vm实例上
		});

	4. 使用<router-view></router-view>标签作为占位标签,在该标签处实现组件的变换

	5. 使用<router-link to="/..."></router-link>其实和<router-view>差不多,不过默认多渲染了一个标签<a>,其中to为目标path路径。注意，如果在path的最后额外增加了查询字符串“?...=...”,也无需改变声明对象中path中的值,依然能够识别。如: /login 和 /login?id="james" 意义相同

		5.1 可以类似<component>标签来使用tag=“”属性来改变默认生成的外部标签

		5.2 可以设置.router-link-active的样式来改变被点击了的router-link的样式(这个类名字也可以由linkActiveClass属性来改变)

		5.3 可以获取查询字符串中的值:
			* 注意，只可以在router控制的component对象中读取到
			在对应的component声明中:
				var login = {
					template: "<h1> Router Test : {{$route.query.id}} </h1>", //可以直接将查询字符串的值用到插值表达式之中
					methods: {
						GetQuery(){
							console.log(this.$route.query.id);	//可以用 .route 获取到vue-router的对象，其中就有query的定义可以读到 ? 后面的数据,这里的.id只是举了个例子,在URL中有发送 ?id="xxx"。
						}
					}
				}

		5.4 传值方法2:
			可以在定义path的时候这样子定义: path="/login/:id", 这样定义了之后，如果输入一个URL长这样: /login/10, 那么这里的 id = 10, 类似于简单的正则表达式(就是用正则表达式实现的)。

			长度可以增加,如: path="/login/:id/:name" , 则: url="/login/10/james"。

	6. 可以使用<transition></transition>标签来包裹实现和包裹<component>标签一样的动画效果,同样也可以设置mode来控制出入的效果。

	7. 实现子路由的嵌套:

		<div id="com1">
			<router-view></router-view>
		</div>

		<div id="com2">
			this is child router.
		</div>

		var componentObject1 = {
			template:"#com1"
		}

		<router-view></router-view>

		var componentObject2 = {
			template:"#com2"
		}

		...

		 var routerObj = new VueRouter({
			routes:[		
				{path: "/", redirect: "/login" },
				{
					path: "/login", 
					component: componentObject1, 
					children:
					[
						{path:"name", component: componentObject2}	
						// 注意child的router的path不需要在开头加"/"，会自动拼接成/login/name,通过这种方法才可以展示componentObject1中的router-view所对应的componentObject2的内容。 如果加了“/”,则会又默认使用了根路径。
					]
				},
			]
		});

		...

	8. 实现一个路径同时多个组件:

		... //实现了defaultCom, topCom, rightCom, bottomCom四个Com的定义

		var router = new VueRouter({
			routes:[
				{
					path:"/",
					components:{
						default: defaultCom,	// default是占用的默认组件
						top: topCom,
						right: rightCom,
						bottom: bottomCom
					}
				}
			]
		})

		<router-view name="top"></router-view>	// 使用name来指定需要使用的组件，如果没有使用name属性的话就会使用default属性所指定的组件

	9. 可以使用watch属性来监视到路由地址的改变：
		var vm = new Vue({
			el:"#div1",
			data:{},
			router: routerObj,
			watch:{
				"$router.path": function(newValue, oldValue){
					//该函数就检测到了路由值的改变
				}
			}
		});`
);