import {BaseData} from "../../base/BaseData.js";

export let JunkRecycle = new BaseData(
    'JavaScript中的垃圾回收机制',
    '如题，介绍一下JS中的垃圾回收',
    `Javascript具有自动垃圾回收机制(GC:Garbage Collecation)。

    原理
    
        垃圾收集器会定期（周期性）找出那些不在继续使用的变量，然后释放其内存。
    
    实现
    
        （1）标记清除法
    
            在函数声明一个变量的时候，就将这个变量标记为“进入环境”。从逻辑上讲，永远都不能释放进入环境的变量作占用的内存，因为只要执行流进入相应的环境，就可能会用到它们。而当变量离开环境时，则将其标记为“离开环境”。垃圾回收器在运行时候会给存储在内存中的所有变量都加上标记。然后它会去掉（环境中的变量以及被环境中的变量引用的变量）的标记（闭包）。在此之后再被标记的变量将被视为准备删除的变量，原因是环境中的变量已经无法访问到这些变量了。最后，垃圾回收器完成内存清楚工作，销毁那些带标记的值并回收他们所占用的内存空间。
    
        例
            ...
    
            function test(){ 
             
             var a = 10 ; //被标记 ，进入环境 
             
             var b = 20 ; //被标记 ，进入环境 
             
            } 
             
            test(); //执行完毕 之后 a、b又被标离开环境，被回收。
    
            ...
    
        （2）引用计数法
    
            引用计数的含义是跟踪记录每个值被引用的次数。当声明了一个变量并将一个引用类型值赋给该变量时，则这个值的引用次数就是1。如果同一个值又被赋给另一个变量，则该值的引用次数加1。相反，如果包含对这个值引用的变量又取得了另外一个值，则这个值的引用次数减1。当这个值的引用次数变成0时，则说明没有办法再访问这个值了，因而就可以将其占用的内存空间回收回来。这样，当垃圾回收器下次再运行时，它就会释放那些引用次数为0的值所占用的内存。
    
            但是很重要的一点是当遇到循环引用的时候，函数的引用次数就不会为0，所以不会被垃圾回收器回收内存，会造成内存泄露。在IE中涉及COM对象，就会存在循环引用的问题。
    
        例
    
            ...
             
            function test(){ 
             
             var a = {} ; //a的引用次数为0 
             
             var b = a ; //a的引用次数加1，为1 
             
             var c =a; //a的引用次数再加1，为2 
             
             var b ={}; //a的引用次数减1，为1 
             
            }
             
            ...`
);