import {BaseData} from "../../base/BaseData.js";

export let CSSSelector = new BaseData(
    "CSS选择器",

    "CSS选择器的整理。",
    
    `.class	
	css版本
		css 1

	例子
		.intro

	例子作用
		选择 class="intro" 的所有元素。

#id	
	css版本
		css 1

	例子
		#firstname

	例子作用
		选择 id="firstname" 的所有元素。

*	
	css版本
		css 2
	
	例子
		*	
	
	例子作用
		选择所有元素。

element	
	css版本
		css 1

	例子
		p	

	例子作用
		选择所有 <p> 元素。

element,element	
	css版本
		css 1

	例子
		div,p

	例子作用
		选择所有 <div> 元素和所有 <p> 元素。

element element	
	css版本
		css 1

	例子
		div p

	例子作用
		选择 <div> 元素内部的所有 <p> 元素。

element>element		
	css版本
		css 2

	例子
		div>p

	例子作用
		选择父元素为 <div> 元素的所有 <p> 元素。

element+element	
	css版本
		css 2 

	例子
		div+p

	例子作用
		选择紧接在 <div> 元素之后的所有 <p> 元素。（只有紧接的第一个会被选择，也就是之后连续还有一个p标签就不会被选择了，然后这个样式会被当成模板全部适用，类似正则）

[attribute]	
	css版本
		css 2
		
	例子
		<style>
			a:[target]{
				background-color: yellow;
			}
		</style>

		<body>
			<a href="www.google.com" target="_blank">
			<a href="www.baidu.com">
		</body>
	例子作用
		选择带有 target 属性所有元素。（上面例子中只有a带有target属性，所以只有上面的链接背景会变成黄色）

[attribute=value]
	css版本
		css 2

	例子
		[target=_blank]

	例子作用
		选择具有属性target且对应值为_blank即target="_blank" 的所有元素。（其它原理同[target]）

[attribute~=value]
	css版本
		css 2

	例子
		[title~=flower]

	例子作用
		选择具有属性title且对应值字符串中包含了单词"flower"的所有元素。（其它原理同[target]）

[attribute|=value]	
	css版本
		css 2

	例子
		[lang|=en]

	例子作用
		选择具有属性lang且对应值字符串是以"en"开头的所有元素。（其它原理同[target]）

:link
	css版本
		css 1

	例子
		a:link

	例子作用
		选择所有未被访问的链接。

:visited	
	css版本
		css 1

	例子
		a:visited

	例子作用
		选择所有已被访问的链接。

:active
	css版本
		css 1

	例子
		a:active

	例子作用
		选择活动链接。（其实就是鼠标或者别的什么按住时的状态）

:hover	
	css版本
		css 1

	例子	
		a:hover

	例子作用
		选择鼠标指针位于其上的链接。

:focus
	css版本
		css 2

	例子
		input:focus	

	例子作用
		选择获得焦点的 input 元素。

:first-letter	
	css版本
		css 1

	例子
		p:first-letter

	例子作用
		选择每个 <p> 元素的首字母。

:first-line	
	css版本
		css 1

	例子
		p:first-line

	例子作用
		选择每个 <p> 元素的首行。

:first-child	
	css版本
		css 2

	例子
		p:first-child

	例子作用
		选择属于父元素的第一个子元素的每个 <p> 元素。（同样类似于正则，当出现一个新的容器（也就是新的父元素如<div>）时，开始寻找（该新的<div>标签内）第一个<p>标签的样式）

:before
	css版本
		css 2

	例子
		p:before

	例子作用
		在每个 <p> 元素的内容之前插入内容。	(类似阴影，是会一直存在的)

:after
	css版本
		css 2

	例子
		p:after

	例子作用
		在每个 <p> 元素的内容之后插入内容。	(类似阴影，是会一直存在的)

:lang(language)	
	css版本
		css 2

	例子
		p:lang(it)

	例子作用
		选择带有以 "it" 开头的 lang 属性值的每个 <p> 元素。（其实就是语言本地化区分，效果和[attribute|=value]很像）

element1~element2
	css版本
		css 3

	例子
		p~ul

	例子作用
		选择前面有 <p> 元素的每个 <ul> 元素。（只要在<ul>元素前面有<p>元素，都会被选择）

[attribute^=value]
	css版本
		css 3

	例子
		a[src^="https"]

	例子作用
		选择其 src 属性值以 "https" 开头的每个 <a> 元素。（暂未发现与[attribute|=value]的区别，大概是版本区别吧？又或者是这个可以配合具体元素使用？）

[attribute$=value]	
	css版本
		css 3

	例子
		a[src$=".pdf"]

	例子作用
		选择其 src 属性以 ".pdf" 结尾的所有 <a> 元素。

[attribute*=value]	
	css版本
		css 3

	例子
		a[src*="abc"]	

	例子作用
		选择其 src 属性中包含 "abc" 子串的每个 <a> 元素。（同样暂未发现与[attribute~=value]的区别）

:first-of-type
	css版本
		css 3

	例子
		p:first-of-type

	例子作用
		选择属于其父元素的首个 <p> 元素的每个 <p> 元素。	（和:first-child效果相同）

:last-of-type
	css版本
		css 3

	例子
		p:last-of-type

	例子作用
		选择属于其父元素的最后 <p> 元素的每个 <p> 元素。	（也就是最后一个）

:only-of-type
	css版本
		css 3

	例子
		p:only-of-type	

	例子作用
		选择属于其父元素唯一的 <p> 元素的每个 <p> 元素。	（也就是只能有一个子元素是<p>的时候生效，出现多个后就无效了，但是出现其它无关元素的时候不影响）

:only-child
	css版本
		css 3

	例子
		p:only-child

	例子作用
		选择属于其父元素的唯一子元素的每个 <p> 元素。（比:only-of-type更加严格一些，只能有一个子元素且当该子元素是<p>的时候才生效）

:nth-child(n)
	css版本
		css 3

	例子
		p:nth-child(2)

	例子作用
		选择属于其父元素的第二个子元素的每个 <p> 元素。（注意不是only，而是直接指明了是第2个元素，且该元素要是<p>）

:nth-last-child(n)	
	css版本
		css 3

	例子
		p:nth-last-child(2)

	例子作用
		同上，从最后一个子元素开始计数。

:nth-of-type(n)	
	css版本
		css 3

	例子
		p:nth-of-type(2)

	例子作用
		选择属于其父元素第二个 <p> 元素的每个 <p> 元素。	（注意和nth-child(n)的区别是这个不严格卡位，只是收集所有的<p>元素，然后数到第二个适用，比nth-child(n)更宽松一些）

:nth-last-of-type(n)
	css版本
		css 3

	例子
		p:nth-last-of-type(2)

	例子作用
		同上，但是从最后一个子元素开始计数。

:last-child	
	css版本
		css 3

	例子
		p:last-child

	例子作用
		选择属于其父元素最后一个子元素每个 <p> 元素。（p:last-child 等同于 p:nth-last-child(1)）

:root
	css版本
		css 3

	例子
		:root

	例子作用
		选择文档的根元素。	（也就是html）

:empty	
	css版本
		css 3

	例子
		p:empty

	例子作用
		选择没有子元素的每个 <p> 元素（包括文本节点，也就是不能有文字内容，如<p></p>才适用，而<p>hello world</p>不适用）。

:target	
	css版本
		css 3

	例子
		#news:target

	例子作用
		选择当前活动的 #news 元素。（就是用<a href="#id">跳转到的位置的当前注目元素）

:enabled	
	css版本
		css 3

	例子
		input:enabled

	例子作用
		选择每个启用的 <input> 元素。（<input type="text" value="Mickey"/>这就是enable）

:disabled
	css版本
		css 3

	例子
		input:disabled

	例子作用
		选择每个禁用的 <input> 元素。（<input type="text" disabled="disabled" value="Disneyland" />这就是disable）

:checked
	css版本
		css 3

	例子
		input:checked

	例子作用
		选择每个被选中的 <input> 元素。（<input type="radio" checked="checked" value="male" name="gender" />这就是checked）

:not(selector)
	css版本
		css 3

	例子
		:not(p)

	例子作用
		选择非 <p> 元素的每个元素。（也就是只要不是<p>元素就用该样式）

::selection	
	css版本
		css 3

	例子
		::selection

	例子作用
		选择被用户选取的元素部分。（就是用鼠标拖动然后选取的文本等地方）`
);