import {BaseData} from "../../../base/BaseData.js";

export let Note = new BaseData(
    'VueRouter笔记',
    '如题，主要记录一些VueRouter的小知识',
    `1. $route 代表的是当前路由，而不是vue-router路由对象

    2. $router 代表的是vue-router路由对象
    
    3. 当前路由类似于当前url，其中包含了当前url的一些常用信息，比如：
        1. $route.params:
    
            在路由配置中以 /:name 作为一个节点，则可以通过 $route.params.name 访问到这个名字。
    
        2. $route.query：
    
            在路由配置中以 /node?param=xxx 作为一个查询的可以通过 $route.query.param 访问到等号后面的东西。
    
    4. 注意多级路由和嵌套路由不一样，
    
        /gsan/test/index 这样子直接匹配的component是在当前根界面中的 <router-view></router-view> 中显示的，这种叫做多级路由。
    
        而嵌套路由是在上一个路由匹配到的component中的 <router-view></router-view> 中显示的，如果没有这个标签就不会显示了，要注意两者的区别。`
);