import {BaseData} from "../../base/BaseData.js";

export let CSSImportant = new BaseData(
    "CSS一些个人想记录的语法",

    "如题，主要属于个人笔记。",
    
    `1.布局相关

	（1）display: inline-block;	//可以控制块元素水平并排

	（2）vertical-align：用于控制行内元素的对其方式（inline，inline-block or table cell），不可用于控制块级元素的对其。
		
		语法：

		/* Keyword values */
		vertical-align: baseline;
		vertical-align: sub;
		vertical-align: super;
		vertical-align: text-top;
		vertical-align: text-bottom;
		vertical-align: middle;
		vertical-align: top;
		vertical-align: bottom;

		/* <length> values */
		vertical-align: 10em;
		vertical-align: 4px;

		/* <percentage> values */
		vertical-align: 20%;

		/* Global values */
		vertical-align: inherit;
		vertical-align: initial;
		vertical-align: unset;

	（3）flex：弹性布局。 需要一个声明了 display:flex 的父容器对象才能生效。
		
		flex这个属性包含了：
			flex-basis（基础宽/高，取决于flex-direction，元素用）
			flex-direction（容器用）
			flex-flow（容器用）
			flex-grow（元素用）
			flex-shrink（元素用）
			flex-wrap（容器用）
			order（元素用）
		以上属性。

		语法：

		/* Keyword values */
		flex: auto;
		flex: initial;
		flex: none;

		/* One value, unitless number: flex-grow */
		flex: 2;

		/* One value, width/height: flex-basis */
		flex: 10em;
		flex: 30%;
		flex: min-content;

		/* Two values: flex-grow | flex-basis */
		flex: 1 30px;

		/* Two values: flex-grow | flex-shrink */
		flex: 2 2;

		/* Three values: flex-grow | flex-shrink | flex-basis */
		flex: 2 2 10%;

		/* Global values */
		flex: inherit;
		flex: initial;
		flex: unset;

	（4）align-content：控制 flexbox’s cross-axis 和 grid’s block 内容的对齐方式，注意是对于多行容器才有效果，单行的容器是没有效果的，所以是把多行容器进行对其。
		
		语法：

		/* Basic positional alignment */
		/* align-content does not take left and right values */
		align-content: center;     /* Pack items around the center */
		align-content: start;      /* Pack items from the start */
		align-content: end;        /* Pack items from the end */
		align-content: flex-start; /* Pack flex items from the start */
		align-content: flex-end;   /* Pack flex items from the end */

		/* Normal alignment */
		align-content: normal;

		/* Baseline alignment */ 
		align-content: baseline;
		align-content: first baseline;
		align-content: last baseline;

		/* Distributed alignment */
		align-content: space-between; /* Distribute items evenly
		                                 The first item is flush with the start,
		                                 the last is flush with the end */
		align-content: space-around;  /* Distribute items evenly
		                                 Items have a half-size space
		                                 on either end */
		align-content: space-evenly;  /* Distribute items evenly
		                                 Items have equal space around them */
		align-content: stretch;       /* Distribute items evenly
		                                 Stretch 'auto'-sized items to fit
		                                 the container */

		/* Overflow alignment */
		align-content: safe center;
		align-content: unsafe center;

		/* Global values */
		align-content: inherit;
		align-content: initial;
		align-content: unset;

	（5）align-items：和align-content的效果几乎相同，但是该属性是对于每一行的容器都进行该行的对齐，不是对整个容器对齐。
		语法：

		/* Basic keywords */ 
		align-items: normal; 
		align-items: stretch; 

		/* Positional alignment */ 
		/* align-items does not take left and right values */
		align-items: center; /* Pack items around the center */ 
		align-items: start; /* Pack items from the start */ 
		align-items: end; /* Pack items from the end */ 
		align-items: flex-start; /* Pack flex items from the start */ 
		align-items: flex-end; /* Pack flex items from the end */

		/* Baseline alignment */
		align-items: baseline; 
		align-items: first baseline; 
		align-items: last baseline; /* Overflow alignment (for positional alignment only) */ 
		align-items: safe center; 
		align-items: unsafe center; 

		/* Global values */
		align-items: inherit; 
	align-items: initial; 
	align-items: unset;

	（6）align-self：就是控制单个元素自身的容器的对齐，而align-items是控制所有元素的对齐方式。（注意对block级别的盒子还有table cell布局无效）
		语法：

		/* Keyword values */ 
		align-self: auto; 
		align-self: normal; 

		/* Positional alignment */ 
		/* align-self does not take left and right values */
		align-self: center; /* Put the item around the center */ 
		align-self: start; /* Put the item at the start */ 
		align-self: end; /* Put the item at the end */ 
		align-self: self-start; /* Align the item flush at the start */ 
		align-self: self-end; /* Align the item flush at the end */ 
		align-self: flex-start; /* Put the flex item at the start */ 
		align-self: flex-end; /* Put the flex item at the end */ 

		/* Baseline alignment */ 
		align-self: baseline; 
		align-self: first baseline; 
		align-self: last baseline; 
		align-self: stretch; /* Stretch 'auto'-sized items to fit the container */ 

		/* Overflow alignment */ 
		align-self: safe center; 
		align-self: unsafe center; 

		/* Global values */ 
		align-self: inherit; 
		align-self: initial; 
		align-self: unset;

	（7）justify-content：同align-content，但是是列对齐。
		语法:

		/* Positional alignment */
		justify-content: center;     /* Pack items around the center */
		justify-content: start;      /* Pack items from the start */
		justify-content: end;        /* Pack items from the end */
		justify-content: flex-start; /* Pack flex items from the start */
		justify-content: flex-end;   /* Pack flex items from the end */
		justify-content: left;       /* Pack items from the left */
		justify-content: right;      /* Pack items from the right */

		/* Baseline alignment */
		/* justify-content does not take baseline values */

		/* Normal alignment */
		justify-content: normal;

		/* Distributed alignment */
		justify-content: space-between; /* Distribute items evenly
		                                   The first item is flush with the start,
		                                   the last is flush with the end */
		justify-content: space-around;  /* Distribute items evenly
		                                   Items have a half-size space
		                                   on either end */
		justify-content: space-evenly;  /* Distribute items evenly
		                                   Items have equal space around them */
		justify-content: stretch;       /* Distribute items evenly
		                                   Stretch 'auto'-sized items to fit
		                                   the container */

		/* Overflow alignment */
		justify-content: safe center;
		justify-content: unsafe center;

		/* Global values */
		justify-content: inherit;
		justify-content: initial;
		justify-content: unset;

	（8）justify-items：同align-items，但是是对列对齐。
		语法：

		/* Basic keywords */
		justify-items: auto;
		justify-items: normal;
		justify-items: stretch;

		/* Positional alignment */
		justify-items: center;     /* Pack items around the center */
		justify-items: start;      /* Pack items from the start */
		justify-items: end;        /* Pack items from the end */
		justify-items: flex-start; /* Pack flex items from the start */
		justify-items: flex-end;   /* Pack flex items from the end */
		justify-items: self-start;
		justify-items: self-end;
		justify-items: left;       /* Pack items from the left */
		justify-items: right;      /* Pack items from the right */

		/* Baseline alignment */
		justify-items: baseline;
		justify-items: first baseline;
		justify-items: last baseline;

		/* Overflow alignment (for positional alignment only) */
		justify-items: safe center;
		justify-items: unsafe center;

		/* Legacy alignment */
		justify-items: legacy right;
		justify-items: legacy left;
		justify-items: legacy center;

		/* Global values */
		justify-items: inherit;
		justify-items: initial;
		justify-items: unset;

	（9）justify-self：同align-self，但是是对列对齐。（注意，在table cell的布局里和 flexbox 的布局里该属性无效，在grid布局里有效）
		语法：

		/* Basic keywords */
		justify-self: auto;
		justify-self: normal;
		justify-self: stretch;

		/* Positional alignment */
		justify-self: center;     /* Pack item around the center */
		justify-self: start;      /* Pack item from the start */
		justify-self: end;        /* Pack item from the end */
		justify-self: flex-start; /* Pack flex item from the start */
		justify-self: flex-end;   /* Pack flex item from the end */
		justify-self: self-start;
		justify-self: self-end;
		justify-self: left;       /* Pack item from the left */
		justify-self: right;      /* Pack item from the right */

		/* Baseline alignment */
		justify-self: baseline;
		justify-self: first baseline;
		justify-self: last baseline;

		/* Overflow alignment (for positional alignment only) */
		justify-self: safe center;
		justify-self: unsafe center;

		/* Global values */
		justify-self: inherit;
		justify-self: initial;
		justify-self: unset;

2.图片相关

	（1）clip-path: margin-box;	//图片裁剪

3.事件相关

	（1）user-select: none;	//控制用户不可选中

		兼容性版本:

		-moz-user-select

		-webkit-user-select

		-ms-user-select

		-khtml-user-select

	（2）pointer-event: none;	//控制是否接收鼠标事件

5.文本相关

	（1）white-space: pre-wrap;	//保留图片的空白字符信息

6.动画相关

	（1）animation: time(s) keyframesName;	//执行动画，关键是这个才可以控制transform属性

		@keyframes animName{	//用开始from的状态和中间的百分比阶段状态还有最后的to状态来共同决定一个动画的过程
			from { ... }
			...
			precentage(number%) { ... }
			...
			to { ... }
		}

（2）transform-origin : 50% 0;	// 用于改变做几何变换的中心点（锚点）

7.全局基础配置相关
	
	（1）@viewport

		概念

			@viewport 规则让我们可以对文档的大小进行设置 viewport 。这个特性主要被用于移动设备，但是也可以用在支持类似“固定到边缘”等特性的桌面浏览器，如微软的Edge。

			按百分比计算尺寸的时候，就是参照的初始视口（viewport）。初始视口指的是任何用户代理和样式对它进行修改之前的视口。桌面浏览器如果不是全屏模式的话，一般是基于窗口大小。

			在移动设备上（或者桌面浏览器的全屏模式），初始视口通常就是应用程序可以使用的屏幕部分。它可能是全屏或者减去由操作系统或者其它应用程序所占用的部分（例如状态栏）。

			@viewport {
			  width: 100vw; /*将视口宽度设为与设备宽度相同*/
			}

		语法

			@规则包含一组包含在CSS代码块中的嵌套的描述符。

			缩放因子1.0 或者 100% 表示不缩放，大于1表示放大，小于1表示缩小。

			描述符

				min-width
					设置viewport的最小宽度

				max-width
					设置viewport的最大宽度
				
				width
					同时设置 min-width 和 max-width
				
				min-height
					设置viewport的最小高度
				
				max-height
					设置viewport的最大高度
				
				height
					同时设置 min-height 和 max-height
				
				zoom
					设置初始缩放系数
				
				min-zoom
					设置最小缩放系数
				
				max-zoom
					设置最大缩放系数
				
				user-zoom
					设置用户是能更改缩放系数
				
				orientation
					控制文档的朝向。

					语法
						/* Keyword values */
						orientation: auto;
						orientation: portrait;/*锁定为纵向*/
						orientation: landscape;/* 锁定为横向*/

				viewport-fit
					控制文档在非矩形屏幕下的显示（控制文档是如何填充满屏幕的）。`,
);