import {BaseData} from "../../base/BaseData.js";

export let Conception = new BaseData(
    'Vue的一些概念',
    '如题，主要记录一些Vue相关的概念',
    `Vue的作用
	
	1.能够帮我们减少不必要的DOM操作（其实DOM操作也就是渲染了）,让程序员集中于业务逻辑的处理。

	2.通过框架提供的指令，前端程序员只需要关心数据的业务逻辑，不用再关心DOM是如何渲染的了

Vue框架使用的设计模式

	使用了MVVM设计模式。

	MVVM概念

		其实是 M（Model） V（View） 和 VM（ViewModel）, MVVM的好处是提供了数据的双向绑定。

		其中VM 是指一个“调度者”，类似于controller。

		M是每个页面中单独的数据。

		V是每个页面的HTML结构。`
);