import {BaseData} from "../../base/BaseData.js";

export let EventBubbleAndCatch = new BaseData(
    
    "事件冒泡和事件捕获",

    "如题，记录知识点",

    `1. 事件捕获:
	从document节点开始向下传递事件到目标target处（搜索标签节点）

2. target:
	target捕获了事件

3. 事件冒泡:
	从target处向父标签层层传递回到document节点，只要注册了相应事件的（如 onclick）都会发生事件的回调，所以不需要这种效果就需要阻止冒泡

注意: 1.2.3 为一次浏览器事件发生的按序处理流程

4.事件代理:
	我们想要在点击每个h5标签时，弹出对应的innerHTML 。常规做法是遍历每个h5,然后在每个h5上绑定一个点击事件，这种做法在h5较少的时候可以使用，但如果有一万个h5，那就会导致性能降低。这时就需要事件代理出场了。

先贴代码

obj1.addEventListener('click',function(e){
    var e=e||window.event;
    if(e.target.nodeName.toLowerCase()=='h5'){
        alert(e.target.innerHTML);
    }
},false);

由于事件冒泡机制，点击了h5后会冒泡到div,此时就会触发绑定在div上的点击事件，再利用target找到事件实际发生的元素，就可以达到预期的效果。

5. 阻止冒泡:
	两种方式来阻止事件冒泡。

方式一：event.stopPropagation();

例子：
	$("#div1").mousedown(function(event){
		event.stopPropagation();
	});

方式二：return false;

例子：
	$("#div1").mousedown(function(event){
		return false;
	});

但是这两种方式是有区别的。return false 不仅阻止了事件往上冒泡，而且阻止了事件本身。event.stopPropagation() 则只阻止事件往上冒泡，不阻止事件本身。
`
);