import {BaseData} from "../../base/BaseData.js";

export let Activity = new BaseData(
    'Vue的一些概念',
    '如题，主要记录一些Vue相关的概念',
    `Vue的生命周期：

	1. beforeCreate();	// 此时data和methods中的数据都没有被初始化

	2. created();	// 此时data和methods都已经被初始化好了

	// 重点： 在created完成了之后，就进入了vue对象的模板编辑阶段。首先会检查vue对象的“el”标签以及该标签对应的值的元素的id是否存在，1.如果不存在，则生命周期暂停，等待vm.$mount(el)被调用，调用之后进入2; 2.如果存在，则检查对象的“ template”标签是否存在，1.如果存在则将template标签里的值编译进“render function”，2.如果不存在就是用"el"标签指定的id的outerHTML作为template。 注意这个阶段，模板还没有被真正的mount到页面之中

	3. beforeMount();	// 表示模板已经编译完成了，但是模板还未被渲染到页面中。同时，在这个阶段的页面元素还没有被真正的替换掉（插值表达式维持原样），保持着html原本的样子输出

	4. mounted();	// 表示内存中的模板已经真实的挂载到了页面中，页面中已经可以看到更新好的页面了。如果想要操作DOM元素节点，那么一定要在mounted阶段中才可以操作。

	5. beforeUpdate();	// 当data（model）上的数据发生变化的时候，才会调用，也就是最少会调用0次。当执行beforeUpdate函数的时候，页面上的数据还是旧的数据，但是data里的数据已经是最新的数据了。在这之后，会将最新的数据在内存中重新构建一个DOM树，然后进行重新渲染。

	6. updated();	// 该函数执行时data和页面上的内容已经都是最新的了

	7. beforeDestroy();	// 执行该函数的时候实例已经从运行阶段进入到了销毁阶段。此时所有data，methods，filters，directives...都还存在，还没有实际被释放。

	8. destroyed();	// 执行该函数的时候，所有实例身上的数据和函数全都都已经被销毁，不可以使用了。

	* 注意，mounted是实例创建的最后一个生命周期函数，当mounted调用过后，实例就已经完全被创建好了

	如何使用上述生命周期函数，例：

		var vm = new Vue({

			el:"#div1",

			data:{

			},

			beforeCreate(){

			},

			created(){

			},

			beforeMount(){

			},

			mounted(){

			}
		});`
);